import React from 'react';
import styled from '@emotion/styled/macro';
import { Flex, Box, Heading, Button } from 'rebass';
import { Input } from '@rebass/forms';
import strings from './strings.json';

const Image = styled.img`
  width: 100%;
  position: absolute;
  z-index: 0;
  transform: translate(-50%, -50%);
  left: 50%;
`;

const FormWrap = styled(Flex)`
  padding: .5em;
  position: relative;
  z-index: 1;
`;

const Flag = styled(Box)`
  content: "";
  position: absolute;
  background: transparent;
  left: 0px;
  transform: translate(0px, 0px);
  width: inherit;
  height: 0px;
  top: 0px;
  border-left: 50vw solid white;
  border-right: 50vw solid white;
`;

const { ariaNewsletterHeader, ariaSubscribeLabel, ariaEmailPlaceholder } = strings;

const Newsletter = () => {
  return (
    <Box sx={{ position: 'relative'}} width={1} bg={"backgroundGrey"} py={["15vw", "7vw"]} px={["5vw", null, "20vw"]}>
      <Box mx="auto">
        <Heading mb=".5em" fontSize={["5vw", "3vw", "2vw"]} textAlign="center">{ ariaNewsletterHeader }</Heading>
        <FormWrap flexDirection={["column", "row"]} bg="background">
          <Input p="1em" width={[1, 7 / 10]} textAlign={["center", "left"]} sx={{ border: 'none' }} type="email" aria-describedby="emailHelp" placeholder={ ariaEmailPlaceholder } />
          <Button variant="primary" width={[1, 3 / 10]} p="1em" fontSize={["4vw", "1.5vw", "1vw"]}>{ ariaSubscribeLabel }</Button>
        </FormWrap>
        
        <Box width={[120, null, 180]} mx="auto" sx={{ position: 'relative'}}>
          <Image src="//cdn.shopify.com/s/files/1/0117/9005/7534/files/fruit2-3.png" alt="decor" />
        </Box>

        <Flag mx={["-5vw", "-3.5vw"]}
          sx={{
            borderBottomWidth: ["3rem", "5em"],
            borderBottomStyle: 'solid',
            borderBottomColor: 'transparent'
          }}
        />
      </Box>
    </Box>
  );
};

export default Newsletter;