import React, { useState, useEffect, useRef } from 'react';
import { useStaticQuery, graphql } from "gatsby";
import { Flex, Box, Text } from 'rebass';
import GatsbyLink from 'gatsby-link';
import styled from '@emotion/styled';
import {
  disableBodyScroll,
  enableBodyScroll,
  clearAllBodyScrollLocks,
} from 'body-scroll-lock';

import { useMenuContext } from './context';
import Burger from '../Icons/Burger';
import Close from '../Icons/Close2';
import TopMenu from '../TopMenu';
import NoImage from '../Icons/NoImage';

const Sidebar = styled(Box)`
  position: absolute;
  z-index: 98;
  top: 20vw;
  left: 0;
  width: 100%;
  height: auto;
  padding: 5vw;
  animation-name: test;
  animation-duration: .2s;
  animation-direction: reverse;
  
  @keyframes test {
    0%   {top:0vw;}
    25%  {top:-25vw;}
    50%  {top:-50vw;}
    75%  {top:-75vw;}
    100% {top:-100vw;}
  }
`;

const Pointer = styled(Box)`
  left: 11vw;
  top: 4vw;
`;

const Carret = styled.img`
  width: 12px;
`;

const BackToMenu = styled(Box)`
  border-bottom: 1px solid #e6e6e6;
  font-size: 1rem;

  &:hover {
    opacity: 0.8;
  }
`;

const Submenubar = styled(Box)`
  position: absolute;
  z-index: 99;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 5vw;
`;



const SubmenuWrap = styled(Flex)`
  flex-direction: column;
  height: inherit;
  position: relative;
  top: .5vw;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  animation-name: example;
  animation-duration: .2s;
  animation-direction: reverse;
  
  @keyframes example {
    0%   {left:0vw;}
    25%  {left:-25vw;}
    50%  {left:-50vw;}
    75%  {left:-75vw;}
    100% {left:-100vw;}
  }
`

const DisabledArea = styled(Box)`
  position: fixed;
  z-index: 97;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #000;
  opacity: 0.4;
  width: 100%;
  height: 100%;
  overflow: 'hidden';
`;

const MenuItem = styled(Box)`
  border-top: 1px solid #e6e6e6;
  padding: 5vw;
  font-size: 1rem;

  &:hover {
    opacity: 0.8;
  }
`;

const SubmenuItem = styled(Box)`
  border-bottom: 1px solid #e6e6e6;
  padding: .5vw 5vw;
  font-size: 1rem;
  height: inherit;

  &:hover {
    opacity: 0.8;
  }
`;


const Image = styled.img`
  width: 16vw;
`;

const MobileMenu = ({ menu: componentMenu }) => {
  const [parentId, setParentId] = useState(0);
  const [showSidebar, setShowsidebar] = useState(false);
  const [showSubmenubar, setShowsubmenubar] = useState(false);

  const updateParentId = (id) => {
    setParentId(id);
  };

  const toggleSidebar = () => {
    setShowsidebar(!showSidebar);
  };

  const toggleSubmenubar = () => {
    setShowsubmenubar(!showSubmenubar);
  };

  const sidebarRef = useRef();

  useEffect(() => {
    if (showSidebar) {
      disableBodyScroll(sidebarRef.current);
    } else {
      enableBodyScroll(sidebarRef.current);
    }
    return () => {
      clearAllBodyScrollLocks();
    };
  }, [showSidebar]);

  const data = useStaticQuery(graphql`
    {
      allShopifyCollection {
        nodes {
          shopifyId
          handle
          title
          fields {
            shopifyThemePath
          }
          image {
            src
          }
        }
      }
    }
  `)

  return (
    <>
      <Box ref={sidebarRef}>
        {showSidebar ? (
          <>
            <Close
              onClick={toggleSidebar}
              width="100%"
              height="100%"
              color="menuText"
              sx={{ cursor: 'pointer' }}
            />
            <Sidebar>
              <Pointer
                width={10}
                height={10}
                sx={{
                  position:'absolute',
                  borderTopWidth: 5,
                  borderTopStyle: 'solid',
                  borderTopColor: 'primary',
                  borderRightWidth: 5,
                  borderRightStyle: 'solid',
                  borderRightColor: 'primary',
                  borderBottomWidth: 5,
                  borderBottomStyle: 'solid',
                  borderBottomColor: 'transparent',
                  borderLeftWidth: 5,
                  borderLeftStyle: 'solid',
                  borderLeftColor: 'transparent',
                  transform: 'rotate(-45deg)'
                }}
              />
              <Flex
                flexDirection="column"
                sx={{
                  borderTopWidth: '2px',
                  borderTopStyle: 'solid',
                  borderTopColor: 'primary',
                  bg: 'menu',
                  boxShadow: '0 0 10px rgba(0, 0, 0, 0.2)',
                }}
              >
                {componentMenu
                  ? componentMenu.map((element) => {
                      if (element.parentId === parentId) {
                        return (
                          <React.Fragment key={element.id}>
                            {element.type === 'header' ? (
                              <Box>
                                <MenuItem
                                  sx={{ cursor: 'pointer' }}
                                  bg="menuItem"
                                  color="menuText"
                                  onClick={toggleSubmenubar}
                                  key={element.id}
                                >
                                  {element.name}
                                  <Carret src='https://cdn.shopify.com/s/files/1/0117/9005/7534/files/slide-arrow2.png' alt='next' style={{ float: 'right' }} />
                                </MenuItem>

                                {/* Submenu */}
                                {showSubmenubar ? (
                                  <Submenubar bg="transparent">
                                    <SubmenuWrap bg="menu">
                                      <BackToMenu
                                        sx={{ cursor: 'pointer' }}
                                        bg="menuItem"
                                        color="menuText"
                                        onClick={toggleSubmenubar}
                                        key={element.id}
                                      >
                                        <Carret src='https://cdn.shopify.com/s/files/1/0117/9005/7534/files/slide-arrow.png' alt='next' style={{ margin: '3vw 5vw', float: 'left' }} />
                                        <Text p="3vw 5vw" sx={{ float: 'left', borderLeftWidth: 1, borderLeftStyle: 'solid', borderLeftColor: 'divider' }}>{element.name}</Text>
                                      </BackToMenu>
                                      {componentMenu ? componentMenu.map((child) => {
                                        if (child.parentId != 0) {
                                        const collectionItemsNumber =
                                          child && child.children && child.children.length > 0
                                            ? child.children.length
                                            : 1;
                                          return (
                                            <React.Fragment key={child.id}>
                                              {child.parentId === element.id ? (
                                                <GatsbyLink
                                                  to={`/${child.type}/${child.handle}`}
                                                  key={child.id}
                                                  style={{ textDecoration: 'none', height: 'inherit' }}
                                                >
                                                  <SubmenuItem
                                                    display="flex"
                                                    width={1}
                                                    bg="menuItem"
                                                    color="menuText"
                                                  >
                                                    {child.type == 'collection' ? data.allShopifyCollection.nodes.map((collection, index) => {
                                                      if (collection.handle === child.handle) {
                                                        return (
                                                          <Image src={collection.image.src} alt={collection.handle} key={index}/>
                                                        )
                                                      } else {
                                                        ''
                                                      }})
                                                    : <NoImage
                                                        sx={{
                                                          borderWidth: '1px',
                                                          borderStyle: 'solid',
                                                          borderColor: 'grey',
                                                        }}
                                                        color="grey"
                                                        p={4}
                                                        width="500px"
                                                        height="500px"
                                                        key={index}
                                                      />
                                                    }
                                                    <Text my="auto" pl="1em">{child.name}</Text>
                                                  </SubmenuItem>
                                                </GatsbyLink>
                                              ) : (
                                                ''
                                              )}
                                            </React.Fragment>
                                          );
                                        } else {
                                          ''
                                        }})
                                      : ''}
                                    </SubmenuWrap>
                                  </Submenubar>
                                ) : (
                                  ''
                                )}
                              </Box>
                            ) : (
                              ''
                            )}
                            {element.type !== 'header' &&
                            element.type !== 'external' ? (
                              <GatsbyLink
                                to={`/${element.type}/${element.handle}`}
                                key={element.id}
                                style={{ textDecoration: 'none' }}
                                onClick={toggleSidebar}
                              >
                                <MenuItem
                                  bg="menuItem"
                                  color="menuText"
                                >
                                  {element.name}
                                </MenuItem>
                              </GatsbyLink>
                            ) : (
                              ''
                            )}
                            {element.type === 'external' ? (
                              <a
                                href={element.link}
                                key={element.id}
                                style={{ textDecoration: 'none' }}
                              >
                                <MenuItem
                                  bg="menuItem"
                                  color="menuText"
                                >
                                  {element.name}
                                </MenuItem>
                              </a>
                            ) : (
                              ''
                            )}
                          </React.Fragment>
                        );
                      } else {
                        return '';
                      }
                    })
                  : ''}
                <Box
                  sx={{
                    borderTopWidth: '1px',
                    borderTopStyle: 'solid',
                    borderTopColor: 'divider',
                    bg: 'menu',
                    fontSize: 11,
                    lineHeight: 1.75,
                    textTransform: 'uppercase'
                  }}
                  p="3vw 5vw"
                >
                  <TopMenu />
                </Box>
              </Flex>
            </Sidebar>
          </>
        ) : (
          <Burger
            width="100%"
            height="100%"
            onClick={toggleSidebar}
            sx={{ cursor: 'pointer' }}
          />
        )}
      </Box>
    </>
  );
};

export default MobileMenu;
