import React, { useState } from 'react';
import { useStaticQuery, graphql } from "gatsby";
import { Flex, Box } from 'rebass';
import GatsbyLink from 'gatsby-link';
import styled from '@emotion/styled';
import NoImage from '../Icons/NoImage';

const Menubar = styled(Box)`
  position: relative;
  z-index: 98;
  height: 100%;
`;

const Pointer = styled(Box)`
  top: .7vw;
`;

const Submenubar = styled(Box)`
  position: fixed;
  z-index: 99;
  width: auto;
  height: 100%;
  left: 0;
  padding: 0 3.5vw;
`;

const MenuItem = styled(Box)`
  padding-right: 1vw;
  padding-left: 1vw;
  padding-top: .5vw;
  padding-bottom: .5vw;
  text-align: center;

  &:hover {
    opacity: 0.8;
  }
`;

const Image = styled.img`
  width: 80%;
  margin: auto;
`;

const Menu = ({ menu: componentMenu }) => {
  const [parentId, setParentId] = useState(0);
  const [showSubmenubar, setShowsubmenubar] = useState(false);

  const updateParentId = (id) => {
    setParentId(id);
  };

  const toggleSubmenubar = () => {
    setShowsubmenubar(!showSubmenubar);
  };

  const data = useStaticQuery(graphql`
    {
      allShopifyCollection {
        nodes {
          shopifyId
          handle
          title
          fields {
            shopifyThemePath
          }
          image {
            src
          }
        }
      }
    }
  `)

  return (
    <>
      <Box>
        <Menubar width={1} sx={{ bg: 'menu' }}>
          <Flex>
            {componentMenu
              ? componentMenu.map((element) => {
                  if (element.parentId === parentId) {
                    return (
                      <React.Fragment key={element.id}>
                        {element.type === 'header' ? (
                          <Box>
                            <MenuItem
                              sx={{ cursor: 'pointer' }}
                              bg="menuItem"
                              color="menuText"
                              onClick={toggleSubmenubar}
                              key={element.id}
                            >
                              {element.name}
                            </MenuItem>

                            {/* Submenu */}
                            {showSubmenubar ? (
                              <Submenubar bg="transparent" sx={{ top: [null, '16vw', '14vw'] }}>
                                <Pointer
                                  width="1vw"
                                  height="1vw"
                                  sx={{
                                    position:'relative',
                                    left: [null, '20vw', '16.5vw'],
                                    borderTopWidth: '.6vw',
                                    borderTopStyle: 'solid',
                                    borderTopColor: 'primary',
                                    borderRightWidth: '.6vw',
                                    borderRightStyle: 'solid',
                                    borderRightColor: 'primary',
                                    borderBottomWidth: '.6vw',
                                    borderBottomStyle: 'solid',
                                    borderBottomColor: 'transparent',
                                    borderLeftWidth: '.6vw',
                                    borderLeftStyle: 'solid',
                                    borderLeftColor: 'transparent',
                                    transform: 'rotate(-45deg)'
                                  }}
                                />
                                <Flex
                                  bg="menu"
                                  p="3.5vw"
                                  sx={{
                                    position: 'relative',
                                    top: 0,
                                    borderTopWidth: "2px",
                                    borderTopStyle: 'solid',
                                    borderTopColor: 'primary',
                                    boxShadow: '0 0 10px rgba(0, 0, 0, 0.2)'
                                  }}
                                >
                                  {componentMenu ? componentMenu.map((child) => {
                                    if (child.parentId != 0) {
                                    const collectionItemsNumber =
                                      child && child.children && child.children.length > 0
                                        ? child.children.length
                                        : 1;
                                      return (
                                        <React.Fragment key={child.id}>
                                          {child.parentId === element.id ? (
                                            <GatsbyLink
                                              to={`/${child.type}/${child.handle}`}
                                              key={child.id}
                                              style={{ textDecoration: 'none' }}
                                            >
                                              <MenuItem
                                                display="flex"
                                                flexDirection="column"
                                                width={1 / collectionItemsNumber}
                                                bg="menuItem"
                                                color="menuText"
                                              >
                                                {child.type == 'collection' ? data.allShopifyCollection.nodes.map((collection, index) => {
                                                  if (collection.handle === child.handle) {
                                                    return (<Image src={collection.image.src} alt={collection.handle} key={index}/>)
                                                  } else {
                                                    ''
                                                  }})
                                                : <NoImage
                                                    sx={{
                                                      borderWidth: '1px',
                                                      borderStyle: 'solid',
                                                      borderColor: 'grey',
                                                    }}
                                                    color="grey"
                                                    p={4}
                                                    width="500px"
                                                    height="500px"
                                                    key={index}
                                                  />
                                                }
                                                {child.name}
                                              </MenuItem>
                                            </GatsbyLink>
                                          ) : (
                                            ''
                                          )}
                                        </React.Fragment>
                                      );
                                    } else {
                                      ''
                                    }})
                                  : ''}
                                </Flex>
                              </Submenubar>
                            ) : (
                              ''
                            )}
                          </Box>
                        ) : (
                          ''
                        )}
                        {element.type !== 'header' &&
                        element.type !== 'external' ? (
                          <GatsbyLink
                            to={`/${element.type}/${element.handle}`}
                            key={element.id}
                            style={{ textDecoration: 'none' }}
                          >
                            <MenuItem
                              bg="menuItem"
                              color="menuText"
                            >
                              {element.name}
                            </MenuItem>
                          </GatsbyLink>
                        ) : (
                          ''
                        )}
                        {element.type === 'external' ? (
                          <a
                            href={element.link}
                            key={element.id}
                            style={{ textDecoration: 'none' }}
                          >
                            <MenuItem
                              bg="menuItem"
                              color="menuText"
                            >
                              {element.name}
                            </MenuItem>
                          </a>
                        ) : (
                          ''
                        )}
                      </React.Fragment>
                    );
                  } else {
                    return '';
                  }
                })
              : ''}
          </Flex>
        </Menubar>
      </Box>
    </>
  );
};

export default Menu;
