import React, { Component } from 'react';
import PropTypes from 'prop-types';

class AccordionSection extends Component {
  static propTypes = {
    children: PropTypes.instanceOf(Object).isRequired,
    isOpen: PropTypes.bool.isRequired,
    label: PropTypes.string.isRequired,
    onClick: PropTypes.func.isRequired,
  };

  onClick = () => {
    this.props.onClick(this.props.label);
  };

  render() {
    const {
      onClick,
      props: { isOpen, label },
    } = this;

    return (
      <div>
        <div onClick={onClick} style={{ position: 'relative', margin: 0, cursor: 'pointer', lineHeight: '2.5', fontWeight: '600', fontSize: '5vw' }}>
          {label}
          <div style={{ float: 'right' }}>
            {!isOpen && <span style={{ position: 'absolute', right: 0, top: '-12px', fontSize: '7vw' }}>+</span>}
            {isOpen && <span style={{ position: 'absolute', right: 0, top: '-12px', fontSize: '7vw' }}>-</span>}
          </div>
        </div>
        {isOpen && (
          <div
            className={"accordionItem"}

            style={{
              marginBottom: 20,
              padding: '0 18px',
              fontWeight: 'normal',
              position: "relative"
            }}
          >
            {this.props.children}
          </div>
        )}
      </div>
    );
  }
}

export default AccordionSection;