import React, { useState } from 'react';
import { Flex, Text, Box } from 'rebass';
import { useStaticQuery, graphql } from 'gatsby';
import GatsbyLink from 'gatsby-link';
import styled from '@emotion/styled';
import { useScrollPosition } from '@n8tb1t/use-scroll-position';

import Menu from './Menu';
import Search from './Search';
import TopMenu from './TopMenu';
import MobileMenu from './MobileMenu';
import Account from './Icons/Account';
import ShoppingBag from './Icons/ShoppingBag2';
import ShoppingCart from './Icons/ShoppingCart';
import useShopifyFunctions from '../hooks/useShopifyFunctions';
import formatPrice from '../utils/formatPrice';
import strings from './strings.json';

const { ariaAccountLabel, ariaShoppingCartLabel, ariaHomaPageLinkLabel } = strings;

const NavbarBase = styled.nav`
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1030;
`;

const Nav = styled(NavbarBase)`
  visibility: ${(props) => (props.show ? 'visible' : 'hidden')};
  transition: all 200ms ${(props) => (props.show ? 'ease-in' : 'ease-out')};
  transform: ${(props) => (props.show ? 'none' : 'translate(0, -100%)')};
`;

const validURL = (str) => {
  const pattern = new RegExp(
    '^(https?:\\/\\/)?' + // protocol
      '((([a-z\\d]([a-z\\d-]*[a-z\\d])*)\\.)+[a-z]{2,}|' + // domain name
      '((\\d{1,3}\\.){3}\\d{1,3}))' + // OR ip (v4) address
      '(\\:\\d+)?(\\/[-a-z\\d%_.~+]*)*' + // port and path
      '(\\?[;&a-z\\d%_.~+=-]*)?' + // query string
      '(\\#[-a-z\\d_]*)?$',
    'i'
  ); // fragment locator
  return !!pattern.test(str);
};

const Navbar = (props) => {
   const [hideNavbarOnScroll, setHideNavbarOnScroll] = useState(true);

   useScrollPosition(
     ({ prevPos, currPos }) => {
       // Note: prevPos.y > -12 is here to fix Nav component disappearing bug
       // due to elastic scrolling/bounce effect in mobile Safari.
       const isShow = currPos.y > prevPos.y || prevPos.y > -12;
       if (isShow !== hideNavbarOnScroll) setHideNavbarOnScroll(isShow);
     },
     [hideNavbarOnScroll],
     null,
     false,
     100
   );

  const data = useStaticQuery(graphql`
    {
      site {
        siteMetadata {
          gatsbyStorefrontConfig {
            storeName
            locales
            currency
            logo {
              url
              width
              height
            }
            menu {
              handle
              id
              link
              name
              parentId
              type
            }
            topNavMenu {
              name
              link
            }
          }
        }
      }
    }
  `);

  const {
    storeName,
    logo,
    menu,
    locales,
    currency,
  } = data.site.siteMetadata.gatsbyStorefrontConfig;
  
  const { checkout } = useShopifyFunctions();
  const { subtotalPrice } = checkout;

  const displaySubtotalPrice = formatPrice(
    Number(subtotalPrice),
    locales,
    currency
  );

  return (
    <Nav show={hideNavbarOnScroll}>
      <Box p={["5vw", "3.5vw"]} width={1} as="nav">
        <Flex px={["5vw", "3.5vw"]} py="1.5vw" flexDirection="column" bg="white" sx={{ boxShadow: '0 0 10px rgba(0, 0, 0, 0.2)'}}>
          {/* Top nav */}
          <Box
            width="30%"
            justifyContent="space-between"
            alignItems="left"
            display={['none', 'flex']}
            sx={{
              textTransform: 'uppercase',
              fontSize: '.8vw'
            }}
            pb="1.5vw"
          >
            <TopMenu />
          </Box>

          {/* Bottom Nav */}
          <Flex
            sx={{ borderTop: ['none', '.1em solid #e6e6e6'] }}
            width={1}
            justifyContent="space-evenly"
            alignItems="center"
            mx="auto"
            pt="1.5vw"
          >
            <Box justifyContent="flex-start" mr="auto" width="30%" display={["flex", "none"]}>
              <Box width="5vw" height="auto">
                <MobileMenu menu={menu}/>
              </Box>

              <Text
                variant="disabled"
                as={GatsbyLink}
                aria-label={ariaAccountLabel}
                to=""
                fontSize="1.2vw"
                sx={{ textDecoration: 'none' }}
                m="auto"
                pl="1vw"
              >
                <Account width={["6vw", "2vw"]} height="auto" color="primary" />
              </Text>
            </Box>

            {/* Site Logo */}
            <Text
              width={["40%", "15%", "12%"]}
              as={GatsbyLink}
              to="/"
              aria-label={ariaHomaPageLinkLabel}
              sx={{ textDecoration: 'none' }}
            >
              {logo ? (
                <img
                  src={logo.url}
                  width={logo.width}
                  height={logo.height}
                  alt={storeName}
                />
              ) : (
                <Flex>
                  <ShoppingBag
                    width={['25px', '30px']}
                    height={['25px', '30px']}
                    color="primary"
                  />

                  <Text
                    ml={2}
                    color="primary"
                    fontSize={[2, 3]}
                    sx={{ display: ['none', 'block'] }}
                  >
                    {storeName}
                  </Text>
                </Flex>
              )}
            </Text>
            
            {/* Menu bar */}
            <Box
              display={["none", "block"]}
              sx={{
                borderLeftWidth: [0, '1px'],
                borderLeftStyle: 'solid',
                borderLeftColor: '#e6e6e6'
              }}
              width={3 / 5}
              fontSize={["4vw", "1.5vw", "1.2vw"]}
              ml="1vw"
            >
              <Menu menu={menu} />
            </Box>

            {/* Site Nav Icons */}
            <Flex justifyContent={["flex-end", "space-evenly"]} ml="auto" width={["30%", "auto"]}>
              <Box 
                sx={{ 
                  borderRightWidth: [0, '1px'],
                  borderRightStyle: 'solid',
                  borderRightColor: '#e6e6e6',
                  textDecoration: 'none'
                }}
                m={["auto", "0 0 0 1vw"]}
                pr="1vw"
              >
                <Search width={["6vw", "2vw"]} height="auto" color="primary" />
              </Box>
              
              <Box
                variant="disabled"
                as={GatsbyLink}
                aria-label={ariaAccountLabel}
                to=""
                fontSize="1.2vw"
                display={["none", "flex"]}
                sx={{ 
                  borderRightWidth: [0, '1px'],
                  borderRightStyle: 'solid',
                  borderRightColor: '#e6e6e6',
                  textDecoration: 'none'
                }}
                ml="1vw"
                pr="1vw"
              >
                <Account width={["6vw", "2vw"]} color="primary" />
                <Text display={["none", null, "block"]} fontSize="1.2vw" sx={{ whiteSpace: 'nowrap' }}>Log In</Text>
              </Box>

              <Box
                as={GatsbyLink}
                aria-label={ariaShoppingCartLabel}
                to="/cart"
                fontSize="1.2vw"
                display={["block", "flex"]}
                style={{ textDecoration: 'none' }}
                ml="1vw"
              >
                <ShoppingBag width={["6vw", "2vw"]} height="auto" color="primary" />
                <Text display={["none", null, "block"]} fontSize="1.2vw" sx={{ whiteSpace: 'nowrap' }}>{ displaySubtotalPrice }</Text>
              </Box>
            </Flex>
          </Flex>
        </Flex>
      </Box>
    </Nav>
  );
};

export default React.memo(Navbar);
