import React from 'react';
import { Flex } from 'rebass';

const Close = props => {
  return (
    <Flex {...props} justifyContent="center">
      <svg 
        width={props.width ? props.width : '100%'}
        height={props.height ? props.height : '100%'}
        aria-hidden="true"
        focusable="false"
        data-prefix="fas"
        data-icon="times"
        className="icon icon-close"
        role="img"
        xmlns="http://www.w3.org/2000/svg"
        viewBox="0 0 16 16"
      >
        <g fill="#222" fillRule="evenodd">
          <path d="M1.636.222l14.142 14.142-1.414 1.414L.222 1.636z"></path>
          <path d="M14.364.222L.222 14.364l1.414 1.414L15.778 1.636z"></path>
        </g>
      </svg>
    </Flex>
  );
};

export default Close;
