/** @jsx jsx */
/* eslint no-unused-vars: 0 */
import React from 'react';
import { jsx, useThemeUI } from 'theme-ui';
import { Flex, Box, Text, Link } from 'rebass';
import { useStaticQuery, graphql } from 'gatsby';
import GatsbyLink from 'gatsby-link';
import Divider from '../components/Divider';
import loadable from '@loadable/component';import Gps from './Icons/Gps';
import Newsletter from './Newsletter';
import Phone from './Icons/Phone';
import Email from './Icons/Email';
import Accordion from './Accordion/Accordion';
// import { SocialIcon } from 'react-social-icons';

const SocialIcon = loadable(() => import('./SocialIcon'));

const validURL = (str) => {
  const pattern = new RegExp(
    '^(https?:\\/\\/)?' + // protocol
      '((([a-z\\d]([a-z\\d-]*[a-z\\d])*)\\.)+[a-z]{2,}|' + // domain name
      '((\\d{1,3}\\.){3}\\d{1,3}))' + // OR ip (v4) address
      '(\\:\\d+)?(\\/[-a-z\\d%_.~+]*)*' + // port and path
      '(\\?[;&a-z\\d%_.~+=-]*)?' + // query string
      '(\\#[-a-z\\d_]*)?$',
    'i'
  ); // fragment locator
  return !!pattern.test(str);
};

function Footer() {
  const data = useStaticQuery(graphql`
    query FooterQuery {
      site {
        siteMetadata {
          gatsbyStorefrontConfig {
            email
            company
            street
            shipper
            location
            phone
            workingDays
            workingHours
            socialNetworks
            payments
            footerLinksShop {
              name
              link
            }
            footerLinksShea {
              name
              link
            }
            footerLinksInfo {
              name
              link
            }
          }
        }
      }
    }
  `);

  const {
    email,
    company,
    street,
    shipper,
    location,
    phone,
    workingDays,
    workingHours,
    socialNetworks,
    payments,
    footerLinksShop,
    footerLinksShea,
    footerLinksInfo,
  } = data.site.siteMetadata.gatsbyStorefrontConfig;

  const { theme } = useThemeUI();

  const year = new Date().getFullYear();

  return (
    <Box bg="backgroundFooter" px={["5vw", "3.5vw"]} fontSize={['4vw', '1.5vw', '1vw']}>
      <Newsletter />

      <Box
        as="footer"
        width={1}
        mx="auto"
        p={["5vw 0", "4vw 0 2vw"]}
        style={{ height: '100%' }}
      >
        <Flex alignItems="center" mb={[2, 3, 4]} flexWrap="wrap">
          <Box
            display={['none', 'flex']}
            width={[1, 1, 4 / 5]}
            justifyContent={['center', 'space-between']}
            flexWrap="wrap"
            mb={3}
          >
            <Box width={[1, 1 / 4, 1 / 5]}>
              <Text mb={'1em'} fontSize={['4.5vw', '2vw', '1.2vw']} fontWeight={'heading'}>Institut Karit Paris</Text>
              <Box>
                <Flex mb={'1em'}><Gps />{street ? '  ' : ''} {street} {shipper ? '  ' : ''} {shipper} {location ? '  ' : ''} {location}</Flex>
                <Flex mb={'1em'}><Phone />{phone ? '  ' : ''} {phone}</Flex>
                <Flex mb={'1em'}><Email />{email ? '  ' : ''} {email}</Flex>
              </Box>
            </Box>

            <Box width='auto'>
              <Text mb={'1em'} fontSize={['4.5vw', '2vw', '1.2vw']} fontWeight={'heading'}>Shop</Text>
              {footerLinksShop
                ? footerLinksShop.map((link, index) => {
                    // If link is valid url use <a>
                    // else use gatsby-link
                    if (validURL(link.link)) {
                      return (
                        <Text key={index} my={'.5em'}>
                          <Link href={link.link}>{link.name}</Link>
                        </Text>
                      );
                    } else {
                      return (
                        <Text key={index} my={'.5em'}>
                          <GatsbyLink to={link.link} sx={theme.variants.link}>
                            {link.name}
                          </GatsbyLink>
                        </Text>
                      );
                    }
                  })
                : ''}
            </Box>

            <Box width='auto'>
              <Text mb={'1em'} fontSize={['4.5vw', '2vw', '1.2vw']} fontWeight={'heading'}>House of Shea</Text>
              {footerLinksShea
                ? footerLinksShea.map((link, index) => {
                    <Text as='h3'>{link.header}</Text>
                    // If link is valid url use <a>
                    // else use gatsby-link
                    if (validURL(link.link)) {
                      return (
                        <Text key={index} my={'.5em'}>
                          <Link href={link.link}>{link.name}</Link>
                        </Text>
                      );
                    } else {
                      return (
                        <Text key={index} my={'.5em'}>
                          <GatsbyLink to={link.link} sx={theme.variants.link}>
                            {link.name}
                          </GatsbyLink>
                        </Text>
                      );
                    }
                  })
                : ''}
            </Box>

            <Box width='auto'>
              <Text mb={'1em'} fontSize={['4.5vw', '2vw', '1.2vw']} fontWeight={'heading'}>Information</Text>
              {footerLinksInfo
                ? footerLinksInfo.map((link, index) => {
                    // If link is valid url use <a>
                    // else use gatsby-link
                    if (validURL(link.link)) {
                      return (
                        <Text key={index} my={'.5em'}>
                          <Link href={link.link}>{link.name}</Link>
                        </Text>
                      );
                    } else {
                      return (
                        <Text key={index} my={'.5em'}>
                          <GatsbyLink to={link.link} sx={theme.variants.link}>
                            {link.name}
                          </GatsbyLink>
                        </Text>
                      );
                    }
                  })
                : ''}
            </Box>
          </Box>

          {/* Footer Links Accordion */}
          <Box width={1} display={['block', 'none']}>
            <Accordion allowMultipleOpen>
              <div label='Institut Karit Paris'>
                <p><Flex mb={'1em'}><Gps />{street ? '  ' : ''} {street} {shipper ? '  ' : ''} {shipper} {location ? '  ' : ''} {location}</Flex></p>
                <p><Flex mb={'1em'}><Phone />{phone ? '  ' : ''} {phone}</Flex></p>
                <p><Flex mb={'1em'}><Email />{email ? '  ' : ''} {email}</Flex></p>
              </div>
              <div label='Shop'>
                {footerLinksShop
                ? footerLinksShop.map((link, index) => {
                  // If link is valid url use <a>
                  // else use gatsby-link
                  if (validURL(link.link)) {
                    return (
                      <Text key={index} my={"3vw"}>
                        <Link href={link.link}>{link.name}</Link>
                      </Text>
                    );
                  } else {
                    return (
                      <Text key={index} my={"3vw"}>
                        <GatsbyLink to={link.link} sx={theme.variants.link}>
                          {link.name}
                        </GatsbyLink>
                      </Text>
                    );
                  }
                })
              : ''}
              </div>
              <div label='The House of Shea'>
                {footerLinksShea
                ? footerLinksShea.map((link, index) => {
                  // If link is valid url use <a>
                  // else use gatsby-link
                  if (validURL(link.link)) {
                    return (
                      <Text key={index} my={"3vw"}>
                        <Link href={link.link}>{link.name}</Link>
                      </Text>
                    );
                  } else {
                    return (
                      <Text key={index} my={"3vw"}>
                        <GatsbyLink to={link.link} sx={theme.variants.link}>
                          {link.name}
                        </GatsbyLink>
                      </Text>
                    );
                  }
                })
              : ''}
              </div>
              <div label='Information'>
                {footerLinksInfo
                ? footerLinksInfo.map((link, index) => {
                  // If link is valid url use <a>
                  // else use gatsby-link
                  if (validURL(link.link)) {
                    return (
                      <Text key={index} my={"3vw"}>
                        <Link href={link.link}>{link.name}</Link>
                      </Text>
                    );
                  } else {
                    return (
                      <Text key={index} my={"3vw"}>
                        <GatsbyLink to={link.link} sx={theme.variants.link}>
                          {link.name}
                        </GatsbyLink>
                      </Text>
                    );
                  }
                })
              : ''}
              </div>
            </Accordion>
          </Box>

          <Box
            display={['flex', 'none', 'flex']}
            width={[1, null, 1 / 5]}
            textAlign={'center'}
            justifyContent={'center'}
            flexDirection={['row', null, 'column']}
            mr="auto"
            pl={2}
            mb={0, null, "auto"}
          >
            <Text mb={'1em'} fontSize={['4.5vw', '2vw', '1.2vw']} fontWeight={'heading'} display={['none', null, 'block']}>Follow Us</Text>
            {socialNetworks
              ? socialNetworks.map((socialNetwork, index) => {
                  return (
                    <Box
                      key={index}
                      sx={{
                        marginX: [0, 'auto'],
                        opacity: 0.8,
                        ':hover,:focus,.active': {
                          opacity: 1,
                        },
                        width: ['10vw', '2vw'],
                        height: ['10vw', '2vw'],
                      }}
                    >
                      <SocialIcon
                        url={socialNetwork}
                        fgColor={'primary'}
                        bgColor={'transparent'}
                      />
                    </Box>
                  );
                })
              : ''}
          </Box>
        </Flex>

        <Divider bg="grey" my={["1em", "2em"]} />

        <Flex>
          <Box width={["100%", "45%"]}>
            <Text textAlign={['center', 'left']} fontSize={["3vw", "1.5vw", "1vw"]}>
               2018 - {year} Tous droits rservs. {company || ''} <a href="https://www.port80webdesign.com/">ecommerce by port80</a>
            </Text>
          </Box>

          <Box display={['none', 'flex', 'none']} justifyContent='center'>
            {socialNetworks
              ? socialNetworks.map((socialNetwork, index) => {
                  return (
                    <Box
                      key={index}
                      sx={{
                        marginLeft: [2, 3],
                        opacity: 0.8,
                        ':hover,:focus,.active': {
                          opacity: 1,
                        },
                        width: '3vw',
                        height: '3vw',
                      }}
                    >
                      <SocialIcon
                        url={socialNetwork}
                        fgColor={'primary'}
                        bgColor={'transparent'}
                      />
                    </Box>
                  );
                })
              : ''}
          </Box>
        </Flex>
      </Box>
    </Box>
  );
}

export default React.memo(Footer);
